-size:15px;color:ghostwhite;margin:0}.home-container .no-movie-container{justify-content:center;align-items:center;display:flex;flex-direction:column;height:80%}.home-container .no-movie-container img{width:40%}.home-container .no-movie-container p{color:#FF565E;font-weight:bold}.home-container .movies-container::-webkit-scrollbar{display:none}.home-container .movie-card{border:20px solid #30444E;background-color:#30444E;border-radius:10px;margin:20px}.home-container .movie-card img{height:100px;width:100px}.home-container .slide-panel{background-color:#286053;height:0%;position:fixed;bottom:0;left:0;right:0;z-index:1}.home-container .slide-panel .card-img-top{width:200px;min-width:100%;z-index:1}.home-container .slide-panel .slide-panel-imageBoard{height:30%;background-repeat:no-repeat;background-size:cover;background-position:center;position:relative}.home-container .slide-panel .slide-panel-imageBoard img{background-color:white;height:250px;min-width:100%;background-image:url(/static/media/coming_soon.70e45f44.gif);background-size:contain;background-repeat:no-repeat;background-position:center}.home-container .slide-panel .slide-panel-imageBoard .card{margin-top:50%}.home-container .slide-panel .slide-panel-detailsBoard{height:70%;background-color:white;overflow-y:auto}.home-container .slide-panel .close-button{position:absolute;background-color:#22343C;font-size:30px;color:#3ED598;right:10px;top:10px;padding:3px;cursor:pointer;width:30px;border-radius:30px}.home-container .slide-panel .small-un-fav-button{background-color:#3ED598;color:#286053;position:absolute;font-size:30px;right:10px;top:50px;padding:3px;padding-top:5px;cursor:pointer;width:30px;border-radius:30px}.home-container .slide-panel .small-fav-button{background-color:#22343C;color:#FF565E;position:absolute;font-size:30px;right:10px;top:50px;padding:3px;padding-top:5px;cursor:pointer;width:30px;border-radius:30px}.home-container .fav-movies-btn{position:fixed;top:25px;right:0;background-color:#30444E;display:flex;justify-content:center;align-items:center;border-bottom-left-radius:50px;height:100px;width:100px;border-top-left-radius:50px}.home-container .fav-movies-btn .close-button{background-color:#22343C;font-size:30px;color:#3ED598;padding:3px;cursor:pointer;width:30px;border-radius:30px}.home-container .fav-movies-btn .small-fav-button{background-color:#22343C;color:#FF565E;font-size:30px;padding:3px;padding-top:5px;cursor:pointer;width:30px;border-radius:30px}.home-container .dontMove{-webkit-animation:none;animation:none}.home-container .move{-webkit-animation:mymove 1.5s;animation:mymove 1.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes mymove{from{height:100%}to{height:20%}}@keyframes mymove{from{height:100%}to{height:20%}}.home-container .slideUp{-webkit-animation:slideup 0.5s;animation:slideup 0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes slideup{from{height:0%;opacity:0.3}to{height:100%;opacity:1}}@keyframes slideup{from{height:0%;opacity:0.3}to{height:100%;opacity:1}}.home-container .slideDown{-webkit-animation:slidedown 0.5s;animation:slidedown 0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes slidedown{from{height:100%;opacity:1}to{height:0%;opacity:0}}@keyframes slidedown{from{height:100%;opacity:1}to{height:0%;opacity:0}}.margin0{margin:0px}.orange-text{color:#FF565E;letter-spacing:2}.one-line{margin-bottom:10px;flex-direction:row}.one-line p{display:contents}.one-line h3{display:contents}@media only screen and (max-width: 600px){.no-movie-container img{width:80% !important}.home-container h1{color:#697172;font-size:larger}.input-container{height:20% !important}.input-container input{height:60px !important;font-size:20px !important}.input-container .search-btn{height:auto !important}.slide-panel .slide-panel-detailsBoard{padding-top:150px}}

